var interstitialAd = null;
let interstitial = {
	load(id) {
		if (id && uni.createInterstitialAd) {
			interstitialAd = uni.createInterstitialAd({
				adUnitId: id
			})
			interstitialAd.onLoad(() => {
				console.log('')
			})
			interstitialAd.onError((err) => {
				console.log('', err)
			})
			interstitialAd.onClose((res) => {
				console.log('', res)
			})
		}
	},
	show() {
		if (interstitialAd) {
			interstitialAd.show().catch((err) => {
				console.error(err)
			})
		}
	}
}

let videoAd = null;
